.brands {
  border-top: 1px solid #D9FFF5; }
  .brands__title {
    padding: 16px;
    margin-top: 0;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    text-transform: uppercase;
    letter-spacing: -0.2px;
    color: #7E7E82;
    background-image: -webkit-gradient(linear, left top, right top, from(#f8f8f8), to(#ffffff));
    background-image: -o-linear-gradient(left, #f8f8f8, #ffffff);
    background-image: linear-gradient(to right, #f8f8f8, #ffffff); }
  .brands__list {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding-left: 16px;
    list-style: none;
    overflow: scroll; }
  .brands__item {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    width: 224px;
    height: 72px;
    padding-left: 16px;
    padding-right: 16px;
    margin-right: 16px;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    background-color: #fff;
    border: 1px solid #EAEAEA;
    border-radius: 6px; }
  .brands__image {
    -ms-flex-negative: 0;
    flex-shrink: 0; }
