.burger {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-negative: 0;
  flex-shrink: 0;
  width: 40px;
  height: 40px;
  padding-left: 10px;
  background-color: #FF3E79;
  border-radius: 50%; }
  .burger::before {
    content: '';
    display: block;
    width: 18px;
    height: 2px;
    background-color: #fff; }
  .burger::after {
    content: '';
    display: block;
    width: 14px;
    height: 2px;
    background-color: #fff; }
  .burger__middle-line {
    width: 10px;
    height: 2px;
    margin-top: 5px;
    margin-bottom: 5px;
    background-color: #fff; }
