.dropdown-menu {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100vw;
  height: 100vh;
  margin-left: -100vw;
  background-color: #fff;
  -webkit-transition: margin-left 300ms;
  -o-transition: margin-left 300ms;
  transition: margin-left 300ms;
  overflow: scroll; }
  .dropdown-menu--open {
    margin-left: 0; }
  .dropdown-menu__header {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 16px; }
  .dropdown-menu__close {
    position: relative;
    width: 40px;
    height: 40px;
    margin-right: 16px;
    font-size: 0;
    background-color: #FF3E79;
    border: none;
    border-radius: 50%;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg); }
    .dropdown-menu__close::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      display: block;
      width: 20px;
      height: 2px;
      margin-left: -10px;
      margin-top: -1px;
      background-color: #fff; }
    .dropdown-menu__close::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      display: block;
      height: 20px;
      width: 2px;
      margin-top: -10px;
      margin-left: -1px;
      background-color: #fff; }
  .dropdown-menu__logo {
    margin-right: auto; }
  .dropdown-menu__search {
    width: 40px;
    height: 40px;
    font-size: 0;
    background-color: #FF3E79;
    background-image: url(./static/search.svg);
    background-repeat: no-repeat;
    background-position: center center;
    border: none;
    border-radius: 50%; }
  .dropdown-menu__navigation {
    margin-bottom: 32px; }
