    height: 32px;
    background-color: #EAEAEA; }
  .user-menu__item {
    margin-left: 16px; }
  .user-menu__link {
    display: block;
    width: 40px;
    height: 40px;
    font-size: 0;
    text-decoration: none;
    background-color: #FF3E79;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    border-radius: 50%; }
    .user-menu__link--order {
      background-image: url(./static/order.png); }
    .user-menu__link--status {
      background-image: url(./static/status.png); }
