.arrow-btn {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  font-size: 0;
  border: 2px solid #FF3E79;
  border-radius: 50%; }
  .arrow-btn::before {
    content: '';
    display: block;
    margin-left: -4px;
    width: 8px;
    height: 8px;
    border: 2px solid #FF3E79;
    border-left: transparent;
    border-bottom: transparent;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg); }

.order-btn {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  width: 120px;
  height: 32px;
  padding-left: 20px;
  padding-right: 20px;
  font-size: 12px;
  font-weight: 900;
  line-height: 24px;
  letter-spacing: -0.15px;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  background-color: #FF3E79;
  border-radius: 16px; }
  .order-btn::after {
    content: '';
    display: inline-block;
    width: 8px;
    height: 8px;
    margin-left: 10px;
    border: 2px solid #fff;
    border-left: transparent;
    border-bottom: transparent;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg); }

.page-main {
  padding-top: 1px;
  padding-bottom: 40px;
  background-color: #F8F8F8; }
  .page-main__main-title {
    padding-left: 12px;
    margin-top: 24px;
    margin-bottom: 24px;
    font-size: 28px;
    line-height: 40px;
    font-weight: 900;
    letter-spacing: -0.6px;
    border: 4px solid transparent;
    border-left: 4px solid #41F6D7; }
  .page-main__navigation {
    margin-left: 16px; }
  .page-main__about-us {
    margin-bottom: 24px; }
