.tech-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-left: 16px;
  list-style: none;
  overflow: scroll; }
  .tech-list__item {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: start;
    -ms-flex-align: start;
    align-items: flex-start;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    padding: 16px;
    margin-right: 16px;
    width: 240px;
    min-height: 160px;
    background-color: #fff;
    border: 1px solid #eaeaea;
    border-radius: 6px; }
  .tech-list__title {
    margin: 0;
    font-size: 16px;
    font-weight: 700;
    line-height: 24px;
    letter-spacing: -0.2px; }

.tech-repair {
  border-top: 1px solid #D9FFF5; }
  .tech-repair__title {
    padding: 16px;
    margin-top: 0;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    text-transform: uppercase;
    letter-spacing: -0.2px;
    color: #7E7E82;
    background-image: -webkit-gradient(linear, left top, right top, from(#f8f8f8), to(#ffffff));
